body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  margin-bottom: 0;
}

.container {
  text-align: center;
}

.jumbotron {
  background-image: url("https://sites.google.com/site/miniaturephersu/_/rsrc/1304969808239/gallerie/il-castello-austriaco-monty-python-e-il-sacro-graal/I%20fart%20in%20your%20general%20direction.jpeg");
  background-size: cover;
  height: 400px;
}

.jumbotron p {
  font-size: 30px;
  margin-top: 20px;
  background-color: white;
}

.card {
  height: 250px;
  width: 250px;
  margin: 10px;
}

